.ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    top: 0;
    z-index: 1;
    list-style-type: none;
}

.Icon {
    height: 50px;
    width: 50px;
    transition: 1500ms;
}

.Icon:hover {
    border-radius: 50%;
    border: 1px solid aliceblue;
    background-color: aliceblue;
    transition: 1500ms;
    height: 150%;
}

.circle {
    border-radius: 50%;
}

.name {
    font-family: 'Ranchers', cursive;
    -webkit-text-stroke: 0.2px black;
    transition: 1500ms;
    color: black;
}

.name:hover {
    transition: 1500ms;
}
.sc-gtssRu {
    text-align: center;
    -webkit-align-content: space-between;
            align-content: space-between;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    font-family: 'Ranchers', cursive;
    position: relative;
    background-color: rgb(223, 32, 102);
}

.product {
    text-align: center;
    -webkit-align-content: space-between;
